.Content {
    margin-top: 16px;
}

.Graphic {
    margin-top: 50px;
    padding-top: 20px;
    width: 100%;
    height: 400px;
    text-align: center;
    background: -webkit-gradient(linear, left top, left bottom, from(deepskyblue), to(azure));
    background: -webkit-linear-gradient(deepskyblue, azure);
    background: -o-linear-gradient(deepskyblue, azure);
    background: linear-gradient(deepskyblue, azure);
    font-weight: bold;
    font-style: italic;
    font-size: 16px;
    position: relative;
}

.Sun {
    text-align: center;
    margin: 30px auto;
    background: #ffd748;
    height: 150px;
    width: 150px;
    border-radius: 50%;
}

.Grass {
    width: 100%;
    height: 35%;
    position: absolute;
    bottom: 0%;
    background: -webkit-gradient(linear, left top, left bottom, from(yellowgreen), to(green));
    background: -webkit-linear-gradient(yellowgreen, green);
    background: -o-linear-gradient(yellowgreen, green);
    background: linear-gradient(yellowgreen, green)
}


